//1.声明导出，必须带有变量声明：var、let等
export let a=1;  //导出a，值为1，类似于commonjs的exports.a=1
export function test() { //导出test，值为函数，类似于commonjs的exports.test=function(){}
  console.log("test")
}
let age=13;
let sex="female"
export {age,sex}  //此处{}不是声明一个对象，而是将age变量的名称作为导出名称，age变量的值作为导出值
                  //相当于：export let age=13，export let sex ="female"
// 最后导出的内容
// {
//   a,
//   test,
//   age,
//   sex
// }